import React from "react";
import { Row, Col } from "react-bootstrap";
import StakingCard from "./StakingCard";
import RewardingCard from "./RewardingCard";
import UnstakingCard from "./UnstakingCard";

import stakingmamba from "../../assets/img/stake-mamba.svg";

const Cards = () => {
  return (
    <>
      <section className="container staking-cards">        
        <div className="stake-card">
          <StakingCard/>
        </div>
        <div className="reward-card">
          <RewardingCard />
        </div>
        <div className="unstake-card">
          <UnstakingCard />
        </div>
      </section>
      <img
        src={stakingmamba}
        alt="stakingmamba"
        style={{
          position: "absolute",
          zIndex: "-1",
          width: 300,
          right: 0,
          top: "10%",
        }}
      />
      <hr style={{ borderColor: "#f1b90c", marginTop: "10px" }} />
    </>
  );
};

export default Cards;
