import React from "react";
import {
  FaTwitter,
  FaTelegram,
  FaInstagram,
  FaLinkedinIn,
  FaYoutube,
  FaDiscord,
  FaMedium,
  FiMail,
} from "react-icons/all";

import PropTypes from "prop-types";

const SocialLink = ({size}) => {
  return (
    <div className="sociallinks">
      <a
        className="TG-link"
        href="https://twitter.com/mambapad_bsc"
        target="blank"
        color="white"
      >
        <FaTwitter
          size={size}
        />
      </a>
      <a
        className="TG-link"
        href="https://t.me/mambapad_official"
        target="blank"
      >
        <FaTelegram
          // color="white"
          size={size}
        />
      </a>
      <a
        className="TG-link"
        href="https://t.me/mambapad_ann"
        target="blank"
        color="white"
      >
        <FaTelegram
          size={size}
        />
      </a>
      <a
        className="TG-link"
        href="https://medium.com/@mambapad"
        target="blank"
        color="white"
      >
        <FaMedium
          size={size}
        />
      </a>
      <a
        className="TG-link"
        href="mailto:support@mambapad.com"
        target="blank"
        color="white"
      >
        <FiMail
          size={size}
        />
      </a>
    </div>
  );
};

SocialLink.propTypes = {
  size: PropTypes.number.isRequired,
};

export default SocialLink;
