import React from "react";
import Countdown from "react-countdown";
import TotalStaked from "./A_TotalStaked";
import { Form } from "react-bootstrap";

const StakingCard = (props) => {
  return (
    <>
      <TotalStaked />
      <section className="staking-card">
        <div className="card-header">
          <h3>REWARD </h3>
        </div>
        <div className="card-body">
          <section className="card-input">
            <p>HOLDER REWARD</p>
            <Form.Control style={{ marginBottom: "20px" }} placeholder="Total Reward (All-Time)"/>
            <p>HOLDER REWARD (24H)</p>
            <Form.Control style={{ marginBottom: "20px" }} placeholder="Total Reward (Last 24 hours)"/>
          </section>         
          <section className="card-counterdown">
            <div className="card-counterdown-time">
              <Countdown date={Date.now() + 5000000} daysInHours  intervalDelay={true}/>
            </div>
            <div className="card-counterdown-unit">              
              <strong>
                HR
              </strong>
              <strong>
                MIN
              </strong>
              <strong>
                SEC
              </strong>
            </div>
          </section>
        </div>
        <button
          class="stake-button"
          
        >
          CLAIM REWARD
        </button>
      </section>
    </>
  );
};

export default StakingCard;
