import React from "react";
import Countdown from "react-countdown";
import TotalStaked from "./A_TotalStaked";
import { Form } from "react-bootstrap";

const StakingCard = (props) => {
  return (
    <>
      <TotalStaked />
      <section className="staking-card">
        <div className="card-header">
          <h3>UNSTAKE </h3>
        </div>
        <div className="card-body">
          <section className="card-input">
            <p>STAKED AMOUNT</p>
            <Form.Control style={{ marginBottom: "20px" }} placeholder="Total amount of staked" />
            <p>UNSTAKE BOX</p>
            <Form.Control style={{ marginBottom: "20px" }} placeholder='Amount of the token'/>
          </section>          
          <section className="card-counterdown">
            <div className="card-counterdown-time">
              <Countdown date={Date.now() + 500000000} />
            </div>
            <div className="card-counterdown-unit">
              <strong>
                DD
              </strong>
              <strong>
                HR
              </strong>
              <strong>
                MIN
              </strong>
              <strong>
                SEC
              </strong>
            </div>
          </section>
        </div>
        <button
          class="stake-button"
          
        >
          UNSTAKE
        </button>
      </section>
    </>
  );
};

export default StakingCard;
